import React, { Component } from 'react';

import Header from './Header';
import Footer from  './Footer';
import Router from '../router';

import './index.css';

class App extends Component {

    render() {
        return (
            <div className="pageWrap">

                <Header/>
                <main className='main'>
                    <Router/>
                </main>
                <Footer/>

            </div>
        )
    }

}

export default App;



